<!--
  내부에서 출력할수있던가?
key는 고유값이여함 => 고유한건 이름밖에 없음
-->
<template>
  <v-card>
    <v-toolbar color="cyan" dark flat>
      <v-app-bar-nav-icon></v-app-bar-nav-icon>

      <v-toolbar-title>로아 수집품</v-toolbar-title>
      <input type="checkbox" v-model="chx" />
      <v-spacer></v-spacer>
      <input type="text" v-model="nickName" placeholder="닉네임 입력" />
      <v-btn icon>
        <v-icon @click="sech()">mdi-magnify</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>

      <template v-slot:extension>
        <v-tabs v-model="tab" align-with-title>
          <v-tabs-slider color="yellow"></v-tabs-slider>

          <v-tab v-for="(item, index) in items" :key="index"
            ><!--탭이름-->
            {{ item.Type }}
          </v-tab>
        </v-tabs>
      </template>
    </v-toolbar>

    <v-tabs-items v-model="tab">
      <v-tab-item v-for="(item, index) in items" :key="index">
        <v-data-table
          :headers="headers"
          :items="items[index].CollectiblePoints"
          :items-per-page="10"
          class="elevation-1"
        ></v-data-table>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>

<script>
import axios from "axios";

import json from "@/data/data.json";
const url = "https://developer-lostark.game.onstove.com";
export default {
  data() {
    return {
      APIKEY:
        "eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiIsIng1dCI6IktYMk40TkRDSTJ5NTA5NWpjTWk5TllqY2lyZyIsImtpZCI6IktYMk40TkRDSTJ5NTA5NWpjTWk5TllqY2lyZyJ9.eyJpc3MiOiJodHRwczovL2x1ZHkuZ2FtZS5vbnN0b3ZlLmNvbSIsImF1ZCI6Imh0dHBzOi8vbHVkeS5nYW1lLm9uc3RvdmUuY29tL3Jlc291cmNlcyIsImNsaWVudF9pZCI6IjEwMDAwMDAwMDAzNjc1OTQifQ.b17jWmvQn7T94k0V2ThNj9w34yEk-U52H92ycQpuqSuk30Oh7-FQK9jQux38CK-wwUUzKGkzzE16UhU8K6EBnyOxdAr6nfhfoC1dLyB-VLILrzXQ5FXWftCo84d8SVLKUjX_DB9SO7aIheNJJbivwWtEphrhWuqShR9ykFu1lSu906V0X96jG-m9zcSZgY32-rF_HXg_RnkvJwgNspfrtNPiVYAfg5e4nRwCpHtPCyBSOdsH9FogoVZxJw_o5BWthfspt2mdjDKBRBtauY9uBYyZ9sYYDn2er0jKdf5MZ1aYi4x6d7A_sMTMEacS7wLG0QOYpF7Z_uE0x_ila3xYpQ",
      headers: [
        { text: "순서", value: "Num" },
        {
          text: "이름",
          align: "start",
          sortable: false,
          value: "PointName",
        },
        { text: "남은수", value: "Point" },
        { text: "최대치", value: "MaxPoint" },
        { text: "설명", value: "Text" },
      ],
      tab: null,
      nickName: "",
      items: [
        { Type: "모코코 씨앗" },
        { Type: "섬의 마음" },
        { Type: "위대한 미술품" },
        { Type: "거인의 심장" },
        { Type: "이그네아의 징표" },
        { Type: "항해 모험물" },
        { Type: "세계수의 잎" },
        { Type: "오르페우스의 별" },
        { Type: "기억의 오르골" },
      ],
      values: [],
      chx: false,
    };
  },
  methods: {
    sech() {
      console.log("이름 : " + this.nickName);
      axios
        .get(url + `/armories/characters/${this.nickName}`, {
          headers: {
            accept: "application/json",
            authorization: "bearer " + this.APIKEY,
          },
        })
        .then((res) => {
          console.log(res.data);
        });
      axios
        .get(url + `/armories/characters/${this.nickName}/collectibles`, {
          headers: {
            accept: "application/json",
            authorization: "bearer " + this.APIKEY,
          },
        })
        .then((res) => {
          this.items = res.data; //모든데이터
          //console.log(this.items);
          if (res.data != null) {
            this.items.forEach((item, index) => {
              //각탭의 새부내용
              let CollectiblePoints = item.CollectiblePoints;
              CollectiblePoints.forEach((CollectiblePoint, index1) => {
                
                CollectiblePoint.Point =
                  CollectiblePoint.MaxPoint - CollectiblePoint.Point;
                let key = json[index].Value[index1][CollectiblePoint.PointName];
                let data = [key][0];

                if (CollectiblePoint.Point != 0) {
                  CollectiblePoint.Text = data.name;
                  CollectiblePoint.Num = data.num;
                } else {
                  CollectiblePoint.Text = "완(完)";
                  CollectiblePoint.Num = "완(完)";
                }
              });
            });
          } else {
            //캐릭터 데이터가 없을경우
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
